:root {

  --background: #071c3d;
  --text: #16acc9;
  --emphasis: #d3e6e8;
  --accent:#f0767d;
}

body { 
  background-color: var(--background);
}


.top{
  position: sticky; 
  top: 0px; 
  width: 10vw; 
}

.bottom {
  position: sticky;
  bottom: 0px;
  width: 110vw;
}



.text{
        font-family: 'Open Sans', sans-serif;
        font-size: 1.125rem; 
        color: var(--emphasis);
}


.websiteHeader{
font-family: 'Open Sans', sans-serif;
    font-size: 4.5rem;
    letter-spacing: 2px;
    color: var(--emphasis);
    font-weight: 700;
    padding: 20px;
    background: var(--background);
}

b {
  background-color: var(--accent);
  color: var(--background); 
  font-family: 'Open Sans', sans-serif;
  padding: 2px; 
}

 
.navigation{
  position: absolute;
  top: 0px;  
  width: 50%;
  margin: 0 auto; 
  
}

.centered-element {
  margin: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 1;
}



